import * as yup from "yup";

const identifyEmailSchema = yup.object({
  email: yup
    .string()
    .email("  .")
    .required("  ."),
});

const identifyPhoneSchema = yup.object({
  phone: yup
    .string()
    .required("  .")
    .matches(
      /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
      "   "
    ),
});

const recoverInitiateSchema = yup.object({
  pw: yup
    .string()
    .required("  .")
    .min(8, "  8  .")
    .max(20, "  20  .")
    .matches(
      /^(?=.*[a-zA-Z])((?=.*\d)|(?=.*\W))(?=.*[!@#$%^*+=-]).{8,16}$/,
      "  8~16, , ,   ."
    ),
  passwordConfirm: yup
    .string()
    .oneOf([yup.ref("pw"), null], "  .")
    .required("  ."),
});

